<template>
  <div id="discount" class="discount">
    <el-breadcrumb separator=">" class="bread">
      <svg class="icon combinedShape" aria-hidden="true">  <use xlink:href="#icon-locate"></use></svg>
      <el-breadcrumb-item :to="{ path: '/' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <el-card>
      <div>
        <span class="sellerStepTtle">1. </span>
      </div>
      <el-row>
        <el-col :span="24">
          <el-card class="box-card mybox" style="width:100%">
            <div slot="header" class="clearfix el-row-header">
              <svg class="icon combinedShape" aria-hidden="true">  <use xlink:href="#icon-yszk_hui"></use></svg><span class="keynote"></span>
            </div>
            <div class="box-card mycard1 detailContent">
              <el-row>
                <el-col :span="6" class="msgName keynote">{{detailInfo.detailVoList[0].receivableNo}}</el-col>
                <el-col :span="6" class="msgName keynote">{{detailInfo.detailVoList[0].orderNo}}</el-col>
                <el-col :span="6" class="msgName">{{detailInfo.detailVoList[0].isseDt | timeTransfer}}</el-col>
                <el-col :span="6" class="msgName">{{detailInfo.detailVoList[0].dueDt | timeTransfer}}</el-col>
              </el-row>
              <el-row>
                <el-col :span="6" class="msgName">{{detailInfo.detailVoList[0].pyeeEnterpriseName}}</el-col>
                <el-col :span="6" class="msgName">{{detailInfo.detailVoList[0].pyerEnterpriseName}}</el-col>
              </el-row>
              <el-row>
                <el-col :span="6" class="msgName">:{{detailInfo.detailVoList[0].isseAmt}}</el-col>
                <el-col :span="6" class="msgName">(%){{detailInfo.detailVoList[0].rate}}</el-col>
              </el-row>
            </div>
          </el-card>
        </el-col>
      </el-row>
      <div>
        <span class="sellerStepTtle">2. </span>
      </div>
      <el-row>
        <el-form ref="discount" :model="bankOption" :label-position="labelPosition" :rules="discountRules">
          <el-col :span="12">
            <el-form-item label="" prop="bankOption">
              <el-select v-model="bankOption.bankSvcr" placeholder="">
                <template v-for="item in bankOption.bankList">
                  <el-option :label="item.bankName" :value="item.bankSvcr"></el-option>
                </template>
              </el-select>
            </el-form-item>
          </el-col>
        </el-form>
      </el-row>

      <el-row>
        <el-col :span="12">
          <el-button type="primary" @click.native.prevent="discount()"></el-button>
        </el-col>
      </el-row>
    </el-card>
  </div>
</template>

<script>
  import LocalStore from '../../../../common/store'
  import Store from '../../vuex/store'

  export default {
    name:'discount',
    mounted:function () {
      document.body.scrollTop = 0;
      document.documentElement.scrollTop = 0;
      this.getDetail()
      this.getBankList();
    },
    data () {
      return {
        bankOption:{
          bankSvcr:'',
          bankList:[]
        },
        detailInfo:{
          detailVoList:[{
            receivableNo:'',
            orderNo:'',
            isseDt:'',
            dueDt:'',
            pyeeEnterpriseName:'',
            pyerEnterpriseName:'',
            isseAmt:'',
            rate:''
          }
          ]
        }
      }
    },
    methods:{
      discount(){
        var discountParam = {
            receivableNo:this.detailInfo.detailVoList[0].receivableNo,       //
            applicantAcctId:LocalStore.fetchUserInfo().acctIds,   //()
            replyerAcctId:this.bankOption.bankSvcr,      //
            discountApplyAmount:this.detailInfo.detailVoList[0].isseAmt //
        }
        this.$http.post('/v1/receivable/discountApply',discountParam,{emulateJSON:true}).then((res) => {
          console.log(res.body);
          var code =  res.body.code;
          if(code != 0){
            return;
          }
          this.$router.push('/allAccounts/discount/detail')
        },(err) => {
          console.log(err);
        })
      },
      getDetail(){
        var detailParam = {
          receivableNo:Store.state.checkIdRece,
          operatorAcctId:''
        }
        this.$http.post('/v1/receivable/receivableInfoWithSerial',detailParam,{emulateJSON:true}).then((res) => {
          console.log(res.body);
          var code =  res.body.code;
          var data =  res.body.data;
          if(code != 0){
            return;
          }
          //
          this.detailInfo = data;
          this.setStatusInfo(data);
        },(err) => {
          console.log(err);
        })
      },
      getBankList(){
        this.$http.post('/v1/receivable/discountApplyBankList').then((res) => {
          console.log(res.body);
          var code =  res.body.code;
          var data =  res.body.data;
          if(code != 0){
            return;
          }
          this.bankOption.bankList = data;
        },(err) => {
          console.log(err);
        })
      }
    }
  }
</script>



