import Vue from 'vue'
import VueRouter from 'vue-router'
import Layout from './components/layout'
import routerConfig from './router.config'
import resource from 'vue-resource'
import ElementUI from 'element-ui'
import 'element-ui/lib/theme-default/index.css'
import constantData from "../../common/const"

Vue.use(VueRouter)
Vue.use(resource)
Vue.use(ElementUI)
Vue.config.productionTip = false

//
const router = new VueRouter({
  // mode:'history',
  routes: routerConfig
})
/**/
Vue.filter('transactionStatus',function(value){
  var readableStatus;
  switch(value){
    case constantData.UNCONFIRMED:readableStatus="";break;
    case constantData.CONFIRMED:readableStatus="";break;
    default:readableStatus="";break;
  }
  return readableStatus;
})
/**/
Vue.filter('receStatus',function(value){
  var readableStatus;
  switch(value){
    case constantData.CONFIRMED:readableStatus="";break;
    case constantData.FORACCEPT:readableStatus="";break;
    case constantData.ACCEPTED:readableStatus="";break;
    case constantData.DISCOUNTED:readableStatus="";break;
    case constantData.FINISH:readableStatus="";break;
    default:readableStatus="";break;
  }
  return readableStatus;
})

/**/
Vue.filter('wayBillStatus',function(value){
  var readableStatus;
  switch(value){
    case constantData.FORSEND:readableStatus="";break;
    case constantData.SENDFORRESPONSE:readableStatus="";break;
    case constantData.SENDED:readableStatus="";break;
    case constantData.ARRIVED:readableStatus="";break;
    case constantData.REJECTED:readableStatus="";break;
    default:readableStatus="";break;
  }
  return readableStatus;
})
/**/
Vue.filter('repoStatus',function(value){
  var readableStatus;
  switch(value){
    case constantData.INFORRESPONSE:readableStatus="";break;
    case constantData.FORIN:readableStatus="";break;
    case constantData.ALREADYIN:readableStatus="";break;
    case constantData.OUTFORRESPONSE:readableStatus="";break;
    case constantData.FOROUT:readableStatus="";break;
    case constantData.ALREADYOUT:readableStatus="";break;
    default:readableStatus="";break;
  }
  return readableStatus;
})
/**/
Vue.filter('repoCertStatus',function(value){
  var readableStatus;
  switch(value){
    case constantData.CANFLOW:readableStatus="";break;
    case constantData.FROZEN:readableStatus="";break;
    case constantData.DISABLED:readableStatus="";break;
    default:readableStatus="";break;
  }
  return readableStatus;
})
/**/
Vue.filter('payingMethod',function(value){
  var readableStatus;
  switch(value){
    case 0:readableStatus="";break;
    case 1:readableStatus="";break;
    default:break;
  }
  return readableStatus;
})
/**/
Vue.filter('timeTransfer',function(value){
  console.log(value);
  if(value===''||value===0){
    return ""
  }
  return new Date(value).toLocaleString().replace(/\//g, "-");
})
/**/
Vue.filter('nullSituation',function(value){
  if(value===''||value===0){
    return "";
  }
  return value;
})

/* eslint-disable no-new */
var app = new Vue({
  el: '#app',
  router,
  template: '<Layout/>',
  components: { Layout }
})



