<template>
  <div id="inResponse" class="box-card">
    <el-breadcrumb separator=">" class="bread">
      <svg class="icon combinedShape" aria-hidden="true">  <use xlink:href="#icon-locate"></use></svg>
      <el-breadcrumb-item :to="{ path: '/' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <el-card>
      <el-row class="el-row-header statePosition">
        <el-col class="buyerColor stateShow"><svg class="icon" aria-hidden="true">  <use xlink:href="#icon-zhuangtai"></use></svg>
          {{repoDetails.curRepoBusiStatus | repoStatus}}</el-col>
      </el-row>
      <el-row>
        <el-col :span="24">
          <el-card class="box-card mybox" style="width:100%">
            <div slot="header" class="clearfix el-row-header">
              <el-row>
                <el-col :span="8">{{repoDetails.repoBusiNo}}</el-col>
                <el-col :span="8">{{repoDetails.operationRecordVoList[0].operateTime | timeTransfer}}</el-col>
              </el-row>
            </div>
            <div class="box-card mycard1 detailContent">
              <el-row>
                <el-col :span="8" class="msgName">{{repoDetails.storeEnterpriseName}}</el-col>
                <el-col :span="8" class="msgName">{{repoDetails.waybillNo | nullSituation}}</el-col>
                <el-col :span="8" class="msgName">{{repoDetails.logisticsEntepsName | nullSituation}}</el-col>
              </el-row>
              <el-row>
                <el-col :span="8" class="msgName">{{repoDetails.productName}}</el-col>
                <el-col :span="8" class="msgName">{{repoDetails.productQuantity}}{{repoDetails.measureUnit | nullSituation}}</el-col>
                <el-col :span="8" class="msgName">{{repoDetails.productTotalPrice}}</el-col>
              </el-row>
            </div>
          </el-card>
        </el-col>
      </el-row>
      <el-row>
        <el-col :span="12">
          <el-button type="primary" @click.native.prevent="inResponse()"></el-button>
        </el-col>
      </el-row>
    </el-card>
  </div>
</template>
<script>
  import Store from '../../vuex/store.js'

  export default {
    name:'inResponse',
    mounted(){
      document.body.scrollTop = 0;
      document.documentElement.scrollTop = 0;
      this.getDetails()
    },
    data () {
      return {
        repoDetails:{
          repoBusiNo:'',
          curRepoBusiStatus:'',
          opgTimeOfCurStatus:'',
          repoEnterpriceName:'',
          repoCertNo:'',
          productName:'',
          productQuantity:'',
          measureUnit: '',
          productTotalPrice:'',
          logisticsEntepsName:'',
          waybillNo:'',
          operationRecordVoList: [{
            operateTime:''
          }]
        }
      }
    },
    methods:{
        getDetails(){
          this.$http.get('/v1/repository/getRepoBusiHistoryList?repoBusinessNo='+Store.state.checkIdRepo).then((res) => {
            console.log(res.body);
            var code =  res.body.code;
            if(code != 0){
              return;
            }
            this.repoDetails = res.body.data
          },(err) => {
            console.log(err);
          })
        },
        inResponse(){
          var param = {repoBusinessNo:Store.state.checkIdRepo}
          this.$http.post('/v1/repository/incomeApplyResponse',param,{emulateJSON:true}).then((res) => {
            console.log(res.body);
            var code =  res.body.code;
            if(code != 0){
              return;
            }
            this.$router.push('/warehousingCompany/repoDetails');
          },(err) => {
            console.log(err);
          })
        }
    }
  }
</script>



