<template>
  <div>
    <div>
      <el-breadcrumb separator=">" class="bread">
        <svg class="icon combinedShape" aria-hidden="true">  <use xlink:href="#icon-locate"></use></svg>
        <el-breadcrumb-item :to="{ path: '/' }"></el-breadcrumb-item>
        <el-breadcrumb-item></el-breadcrumb-item>
      </el-breadcrumb>
    </div>
    <el-card>
      <el-tabs v-model="activeName" @tab-click="handleClick">
        <el-tab-pane label="" name="first">
          <order-table :orderList="allOrder" status="all" pageSize="10"></order-table>
        </el-tab-pane>
        <el-tab-pane label="" name="second">
          <order-table :orderList="allOrder" status="forConfirm" pageSize="10"></order-table>
        </el-tab-pane>
        <el-tab-pane label="" name="third">
          <order-table :orderList="allOrder" status="forPay" pageSize="10"></order-table>
        </el-tab-pane>
        <el-tab-pane label="" name="fourth">
          <order-table :orderList="allOrder" status="forSend" pageSize="10"></order-table>
        </el-tab-pane>
        <el-tab-pane label="" name="fifth">
          <order-table :orderList="allOrder" status="forReceive" pageSize="10"></order-table>
        </el-tab-pane>
      </el-tabs>
    </el-card>
  </div>
</template>
<script>
  import store from '../../vuex/store.js'
  import constantData from '../../../../common/const'
  import '../../../../assets/css/style.css'
  import OrderTable from '../../components/orderDataTable.vue'
  export default {
    name: 'index',
    data () {
      return {
//          activeName:'first',
        allOrder: [{
          orderNo: "20170403123456",
          orderGenerateTime: '2017-04-03 10:29:11',
          payeeAccount: '',
          productName: '',
          totalPrice: '20,000',
          productNum: '1,000',
          payingMethod: '',
        }
        ]
      }
    },
    components: {
      OrderTable
    },
    computed: {
      state () {
        return store.state;
      },
      constantData () {
        return constantData;
      },
      activeName () {
        if (store.state.isBuyer === 'false') {/*tab*/
          return store.state.orderTab;
        }
        else {
          return "first";
        }
      }
    },
    methods: {
      handleClick () {
        console.log("");
      },
      checkDetail (orderNo) {
//          store.state.checkId=orderNo;
        store.commit('setCheckIdOrder', orderNo);
        console.log(store.state.checkIdOrder);
        this.$router.push("/order/orderDetail");
      },
      confirmOrder (orderNo) {
        console.log("");
        store.commit('setCheckIdOrder', orderNo);
        this.$router.push("/order/confirmOrder");
      },
      signBill () {
        console.log("");
      },
      sendGood () {
        console.log("");
      },
      acceptBill () {
        console.log("");
      }
    },
    mounted () {
      document.body.scrollTop = 0;
      document.documentElement.scrollTop = 0;
      console.log(this.activeName);
      var tempRole;
      switch (store.state.isBuyer) {
        case "true":
          tempRole = 0;
          break;/*0*/
        case "false":
          tempRole = 1;
          break;
        default:
          break;
      }
      this.$http.get("../v1/order/order_list/" + tempRole).then(function (res) {
//        this.$http.get("http://localhost/server_test/getData1.php").then(function(res){
          console.log(": " + res.body.data.length);
          this.allOrder = res.body.data;
        },
        function (err) {
          console.log(err);
        }
      );
    },
  }
</script>
<style>
  .mycard {
    width: 90% !important;
    float: inherit !important;
    margin: 10px auto !important;
    background-color: rgba(242, 242, 242, 1)
  }

  .mycard .el-row {
    margin-bottom: 0 !important;
    display: -webkit-flex;
    flex-flow: row wrap;
    -webkit-flex-flow: row wrap;
    justify-content: flex-start;
    -webkit-justify-content: flex-start;
  }

  .mycard .el-col {
    text-align: center;
    box-sizing: border-box;
    padding: 5px 0;
  }

  .row-black {
    background-color: rgba(215, 215, 215, 1);
  }

  .row-padding {
    box-sizing: border-box;
    padding-top: 10px;
    padding-bottom: 10px;
  }

  .btn-vertical {
    margin-top: 10px;
  }
</style>



