<template>
  <div>
    <el-breadcrumb separator=">" class="bread">
      <svg class="icon combinedShape" aria-hidden="true">  <use xlink:href="#icon-locate"></use></svg>
      <el-breadcrumb-item :to="{ path: '/' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item :to="{ path: '/warehousing/myReceipts' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <el-card>
      <el-row class="el-row-header statePosition">
        <el-col class="buyerColor stateShow "><svg class="icon" aria-hidden="true">  <use xlink:href="#icon-zhuangtai"></use></svg> {{receiptsDetails.repoCertStatus | repoCertStatus}}</el-col>
      </el-row>
      <el-row>
        <el-col :span="24">
          <el-card class="box-card mybox" style="width:100%">
            <div slot="header" class="clearfix el-row-header">
              <svg class="icon" aria-hidden="true">  <use xlink:href="#icon-danzi"></use></svg><span class="keynote"></span>
            </div>
            <div class="box-card mycard1 detailContent">
              <el-row>
                <el-col :span="6" class="msgName keynote">{{receiptsDetails.repoCertNo | nullSituation}}</el-col>
              </el-row>
              <el-row>
                <el-col :span="6" class="msgName">{{receiptsDetails.storerName}}</el-col>
                <el-col :span="6" class="msgName">{{receiptsDetails.holderName}}</el-col>
                <el-col :span="6" class="msgName">{{receiptsDetails.repoEnterpriseName}}</el-col>
                <el-col :span="6" class="msgName">{{receiptsDetails.repoCreateDate}}</el-col>
              </el-row>
              <el-row>
                <el-col :span="6" class="msgName">{{receiptsDetails.productName}}</el-col>
                <el-col :span="6" class="msgName">{{receiptsDetails.productQuantity}}{{receiptsDetails.measureUnit}}</el-col>
                <el-col :span="6" class="msgName">(){{receiptsDetails.productTotalPrice}}</el-col>
              </el-row>
              <el-row>
                <el-col :span="8" class="msgName"></el-col>
              </el-row>


              <!--<el-row v-for="item in receiptsDetails.repoCertHisList">
                <el-col :span="8" class="msgName">{{item.state | repoCertStatus}}{{item.operateTime | timeTransfer}}</el-col>
              </el-row>-->

              <el-row class="collapseTop">
                <template v-for="(item,index) in receiptsDetails.repoCertHisList">
                  <el-row class="status-list" :class="{circleBlue:index==(receiptsDetails.repoCertHisList.length-1)}">
                    <el-col :span="8" :class="{colorBlue:index==(receiptsDetails.repoCertHisList.length-1)}"><span>{{item.state | repoCertStatus}}{{item.operateTime | timeTransfer}}</span></el-col>
                  </el-row>
                </template>
              </el-row>

            </div>
          </el-card>
        </el-col>
      </el-row>
    </el-card>
  </div>
</template>
<script>
  import store from '../../vuex/store'
  import constantData from '../../../../common/const'
  export default {
    name:'index',
    data () {
      return {
        receiptsDetails:''
      }
    },
    computed:{
        constantData () {
            return constantData;
        }
    },
    mounted () {
      document.body.scrollTop = 0;
      document.documentElement.scrollTop = 0;
        this.$http.get("../v1/repository/getRepoCert?repoCertNo="+store.state.checkIdRepoCert).then(function(res){
//            
          console.log(res.body);
          this.receiptsDetails=res.body.data;
        },function(err){
            console.log(err);
        });
    }
  }
</script>



