<template>
  <div id="deliver" class="deliver">
    <div>
      <el-breadcrumb separator=">" class="bread">
        <svg class="icon combinedShape" aria-hidden="true">  <use xlink:href="#icon-locate"></use></svg>
        <el-breadcrumb-item :to="{ path: '/' }"></el-breadcrumb-item>
        <el-breadcrumb-item></el-breadcrumb-item>
        <el-breadcrumb-item></el-breadcrumb-item>
      </el-breadcrumb>
    </div>
    <el-card>
      <div>
        <span class="sellerStepTitle">1. </span>
      </div>
      <el-row class="dataTable">
        <el-row class="el-row-header">
          <el-col :span="6" style="margin-left: 19px">{{sendGoodForm.orderNo}}</el-col>
          <el-col :span="6">{{sendGoodForm.launchOrderTime | timeTransfer}}</el-col>
        </el-row>
        <el-row>
          <el-col :span="6" class="msgName" style="margin-left: 19px">{{sendGoodForm.receiverEnterpriseName}}</el-col>
          <el-col :span="6" class="msgName">{{sendGoodForm.productValue}}</el-col>
          <el-col :span="6" class="msgName">{{sendGoodForm.payingMethod | payingMethod}}</el-col>
        </el-row>
        <el-row>
          <el-col :span="6" class="msgName" style="margin-left: 19px">{{sendGoodForm.productName}}</el-col>
          <el-col :span="6" class="msgName">{{sendGoodForm.productQuantity}}</el-col>
          <el-col :span="6" class="msgName">{{sendGoodForm.productUnitPrice}}</el-col>
        </el-row>
        <el-row>
          <el-col :span="6" class="msgName" style="margin-left: 19px">{{sendGoodForm.payerBank}}</el-col>
          <el-col :span="8" class="msgName">{{sendGoodForm.payerAccount}}</el-col>
        </el-row>
      </el-row>
      <div>
        <span class="sellerStepTitle">2. </span>
      </div>
      <el-row class="dataTable">
        <el-row class="el-row-header">
          <el-col :span="6" style="margin-left: 19px"></el-col>
        </el-row>
        <el-row>
          <el-col :span="6" class="msgName" style="margin-left: 19px">{{sendGoodForm.senderEnterpriseName}}</el-col>
          <el-col :span="6" class="msgName">(){{sendGoodForm.productValue}}</el-col>
          <el-col :span="6" class="msgName">{{sendGoodForm.payingMethod | payingMethod}}</el-col>
        </el-row>
        <el-row>
          <el-col :span="6" class="msgName" style="margin-left: 19px">{{sendGoodForm.productName}}</el-col>
          <el-col :span="6" class="msgName">{{sendGoodForm.productQuantity}}</el-col>
          <el-col :span="6" class="msgName">(){{sendGoodForm.productValue}}</el-col>
        </el-row>
        <el-row>
          <el-col :span="6" class="msgName" style="margin-left: 19px">{{sendGoodForm.senderRepoEnterpriseName}}</el-col>
          <el-col :span="8" class="msgName">{{sendGoodForm.senderRepoCertNo}}</el-col>
        </el-row>
      </el-row>
      <el-row class="dataTable">
        <el-row class="el-row-header">
          <el-col :span="6" style="margin-left: 19px"></el-col>
        </el-row>
        <el-row>
          <el-col :span="6" class="msgName" style="margin-left: 19px">{{sendGoodForm.receiverEnterpriseName}}</el-col>
          <el-col :span="6" class="msgName">{{sendGoodForm.receiverRepoEnterpriseName}}</el-col>
        </el-row>
      </el-row>
      <div>
        <span class="sellerStepTitle">3. </span>
      </div>
      <el-form :model="sendGoodForm1" ref="sendGoodForm1">
        <el-tabs v-model="activeName" type="card" @tab-click="">
        <el-tab-pane label="" name="first">
          <el-col :span="12">
            <el-form-item label="">
              <el-select v-model="sendGoodForm1.logisticsEnterpriseName" placeholder="" style="width:50%">
                <template v-for="item in sendGoodForm1.logisticsList">
                  <el-option :label="item" :value="item"></el-option>
                </template>
              </el-select>
            </el-form-item>
          </el-col>
        </el-tab-pane>
        <el-tab-pane label="" name="second">
          <el-col :span="12">
            <el-form-item label=""><el-input v-model="sendGoodForm1.logistics1"></el-input></el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item label=""><el-input v-model="sendGoodForm1.No"></el-input></el-form-item>
          </el-col>
        </el-tab-pane>
      </el-tabs>
      </el-form>
      <el-button type="primary" @click="confirmSend"></el-button>
    </el-card>
  </div>
</template>
<script>
  import store from '../../vuex/store'
  export default {
    name: 'index',
    data () {
      return {
        sendGoodForm: {
          orderNo:'',
          senderEnterpriseName:'',
          receiverEnterpriseName:'',
          productName:'',
          productQuantity:'',
          productValue:'',
          senderRepoEnterpriseName:'',
          senderRepoCertNo:'',
          receiverRepoEnterpriseName:'',
          receiverRepoBusinessNo:'',

          logisticsEnterpriseName:'',
          logisticsList:''
        },
        sendGoodForm1:{
          logisticsEnterpriseName:'',
          logistics1:'',
          logisticsList:'',
          No:''
        },
        activeName: "first",
        orderDetail:''
      }
    },
    methods:{
        confirmSend () {
          this.sendGoodForm.logisticsEnterpriseName = this.sendGoodForm1.logisticsEnterpriseName;
          this.sendGoodForm.logisticsList = this.sendGoodForm1.logisticsList;
          console.log(this.sendGoodForm)
            this.$http.post("../v1/waybill/unConfirmedWaybill",this.sendGoodForm,{emulateJSON:true}).then(function(res){
                console.log(res.body);
              store.commit('setCheckIdOrder',this.sendGoodForm.orderNo);
              console.log(store.state.checkIdOrder);
              this.$router.push("/logistics/wayBillDetails");
            },function(err){
                console.log(err);
            });
        }
    },
    mounted () {
      document.body.scrollTop = 0;
      document.documentElement.scrollTop = 0;
        /**/
        this.$http.get("../v1/account/allEnterpriseName?roleCode=1").then(function(res){
            this.sendGoodForm1.logisticsList=res.body.data;
        },function(err){
            console.log(err);
        });
        /**/

      this.$http.get("../v1/order/detail?orderNo=" + store.state.checkIdOrder).then(
        function (res) {
          // 
          console.log(res.body);
          this.orderDetail = res.body.data;
          this.sendGoodForm.orderNo=this.orderDetail.txDetail.orderId;/*	*/
          console.log("sendGoodForm: "+this.sendGoodForm.orderNo);
          this.sendGoodForm.launchOrderTime=this.orderDetail.txDetail.operationRecordVoList[0].operateTime;/**/
          this.sendGoodForm.payingMethod=this.orderDetail.txDetail.payingMethod;/**/
          this.sendGoodForm.payerBank=this.orderDetail.txDetail.payerBank;/**/
          this.sendGoodForm.payerAccount=this.orderDetail.txDetail.payerAccount;/**/
          this.sendGoodForm.senderEnterpriseName=this.orderDetail.txDetail.payeeCompanyName;/*	*/
          this.sendGoodForm.receiverEnterpriseName=this.orderDetail.txDetail.payerCompanyName;/*	*/
          this.sendGoodForm.productName=this.orderDetail.txDetail.productName;
          this.sendGoodForm.productQuantity=this.orderDetail.txDetail.productQuantity;
          this.sendGoodForm.productValue=this.orderDetail.txDetail.productTotalPrice;/**/
          this.sendGoodForm.productUnitPrice=this.orderDetail.txDetail.productUnitPrice;/**/
          this.sendGoodForm.senderRepoEnterpriseName=this.orderDetail.txDetail.payeeRepo;/*	*/
          this.sendGoodForm.senderRepoCertNo=this.orderDetail.txDetail.payeeRepoCertNo;/*	*/
          this.sendGoodForm.receiverRepoEnterpriseName=this.orderDetail.txDetail.payerRepo;/*	*/
          this.sendGoodForm.receiverRepoBusinessNo=this.orderDetail.payeeRepoBusinessNo;/*	*/
        }, function (err) {
          // 
          console.log(err);
        }
      );
    }
  }
</script>
<style>
  .el-row {
    margin-bottom: 20px;
  }

  .el-row:last-child {
    margin-bottom: 0;
  }

  .el-col {
    border-radius: 4px;
  }

  .bg-purple-dark {
    background: #99a9bf;
  }

  .bg-purple {
    background: #d3dce6;
  }

  .bg-purple-light {
    background: #e5e9f2;
  }

  .grid-content {
    border-radius: 4px;
    min-height: 36px;
  }

  .row-bg {
    padding: 10px 0;
    background-color: #f9fafc;
  }
</style>



