<template>
  <div>
    <el-breadcrumb separator=">" class="bread">
      <svg class="icon combinedShape" aria-hidden="true">  <use xlink:href="#icon-locate"></use></svg>
      <el-breadcrumb-item :to="{ path: '/' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <el-card>
      <el-row class="el-row-header statePosition">
        <el-col class="buyerColor stateShow"><svg class="icon" aria-hidden="true">  <use xlink:href="#icon-zhuangtai"></use></svg>
          :{{repoDetails.curRepoBusiStatus | repoStatus}}</el-col>
      </el-row>
      <el-row>
        <el-col :span="24">
          <el-card class="box-card mybox" style="width:100%">
            <div slot="header" class="clearfix el-row-header">
              <svg class="icon detailIcon" aria-hidden="true">  <use xlink:href="#icon-cc_H"></use></svg>
              <span class="keynote"></span>
            </div>
            <div class="box-card mycard1 detailContent">
              <el-row>
                <el-col :span="8" class="msgName keynote">{{repoDetails.repoBusiNo}}</el-col>
                <el-col :span="8" class="msgName">{{repoDetails.curRepoBusiStatus | repoStatus}}</el-col><!---->
                <el-col :span="8" class="msgName" v-if="state.isBuyer==='true'">{{repoDetails.inRepoTime | timeTransfer}}</el-col><!---->
                <el-col :span="8" class="msgName" v-else>{{repoDetails.outRepoTime | timeTransfer}}</el-col><!---->
              </el-row>
              <el-row>
                <el-col :span="8" class="msgName keynote" v-if="repoDetails.repoCertNo===''"></el-col>
                <el-col :span="8" class="msgName keynote" v-else>{{repoDetails.repoCertNo}}</el-col>
                <el-col :span="8" class="msgName">{{repoDetails.repoEnterpriceName}}</el-col>
              </el-row>
              <el-row>
                <el-col :span="8" class="msgName">{{repoDetails.productName}}</el-col>
                <el-col :span="8" class="msgName">{{repoDetails.productQuantity}}</el-col>
                <el-col :span="8" class="msgName">(){{repoDetails.productTotalPrice}}</el-col>
              </el-row>
              <el-row>
                <el-col :span="8" class="msgName">{{repoDetails.logisticsEntepsName | nullSituation}}</el-col>
                <el-col :span="8" class="msgName">{{repoDetails.waybillNo | nullSituation}}</el-col>
              </el-row>
              <el-row>
                <el-col :span="8" class="msgName"></el-col>
              </el-row>

              <!--<el-row v-for="item in repoDetails.operationRecordVoList">
                <el-col :span="8" class="msgName">{{item.state | repoStatus}}{{item.operateTime | timeTransfer}}</el-col>
              </el-row>-->

              <el-row class="collapseTop">
                <template v-for="(item,index) in repoDetails.operationRecordVoList">
                  <el-row class="status-list" :class="{circleBlue:index==(repoDetails.operationRecordVoList.length-1)}">
                    <el-col :span="24" :class="{colorBlue:index==(repoDetails.operationRecordVoList.length-1)}"><span>{{item.state | repoStatus}}{{item.operateTime | timeTransfer}}</span></el-col>
                  </el-row>
                </template>
              </el-row>

            </div>
          </el-card>
        </el-col>
      </el-row>
    </el-card>
  </div>
</template>
<script>
  import store from '../../vuex/store'
  import constantData from '../../../../common/const'
  export default {
    name:'index',
    data () {
      return {
        repoDetails:{
          serialNumber:'20170403123456',
          state:'',
          storageTime:'2017-08-01',
          operationRecordVoList:[],
          inRepoTime:'',
          outRepoTime:''
        }
      }
    },
    computed:{
      state () {
          return store.state
      }
    },
    mounted () {
      document.body.scrollTop = 0;
      document.documentElement.scrollTop = 0;
        this.$http.get("../v1/repository/getRepoBusiHistoryList?repoBusinessNo="+store.state.checkIdRepo).then(function(res){
//            
          console.log(res.body);
          this.repoDetails=res.body.data;
          this.repoDetails.inRepoTime='';//
          this.repoDetails.outRepoTime='';
          for(var item in this.repoDetails.operationRecordVoList){
              var temp=this.repoDetails.operationRecordVoList[item];
              if(temp.state===constantData.ALREADYIN){/**/
                console.log("");
                  this.repoDetails.inRepoTime=temp.operateTime;
                  break;
              }
          }
          for(var item in this.repoDetails.operationRecordVoList){
              var temp=this.repoDetails.operationRecordVoList[item];
              if(temp.state===constantData.ALREADYOUT){/**/
                console.log("");
                  this.repoDetails.outRepoTime=temp.operateTime;
                  break;
              }
          }
        },function(err){
            console.log(err)
        });
    }
  }
</script>



