<template>
  <div id="repoDataTable">
    <el-row class="el-row-header" style="background-color: rgb(229,241,245)">
      <el-col :span="4" style="margin-left: 19px"></el-col>
      <el-col :span="4"></el-col>
      <el-col :span="4"></el-col>
      <el-col :span="4"></el-col>
      <el-col :span="4"></el-col>
      <el-col :span="2"></el-col>
    </el-row>
    <template v-for="(item,index) in showData">
      <div>
        <el-row class="dataTable">
          <el-row class="el-row-header">
            <el-col :span="8" style="margin-left: 19px;">{{item.repoBusiNo | nullSituation}}</el-col>
            <el-col :span="8">{{item.orderNo | nullSituation}}</el-col>
          </el-row>
          <el-row class="el-row-content">
            <el-col :span="4" style="margin-left: 19px;">
              <el-row>{{item.productName}}</el-row>
              <el-row>{{item.productQuantity}}</el-row>
            </el-col>
            <el-col :span="4">
              <el-row>{{item.repoEnterpriceName}}</el-row><!---->
            </el-col>
            <el-col :span="4">
              <el-row>{{item.curRepoBusiStatus | repoStatus}}</el-row><!---->
            </el-col>
            <el-col :span="4">
              <el-row v-if="item.repoCertNo===''"></el-row><!---->
              <el-row v-else>{{item.repoCertNo}}</el-row><!---->
            </el-col>
            <el-col :span="4">
              <el-row>{{item.repoCertStatus | repoCertStatus | nullSituation}}</el-row><!---->
            </el-col>
            <el-col :span="3">
              <el-button size="small" @click.native.prevent="checkDetail(item.repoBusiNo)"></el-button>
            </el-col>
          </el-row>
        </el-row>
      </div>
    </template>
    <el-pagination
      layout="total,prev, pager, next,jumper"
      @current-change="currentChange"
      :total="tableData.length">
    </el-pagination>

  </div>
</template>

<script>
  import store from '../vuex/store'
  import constantData from '../../../common/const'
  import '../../../assets/css/style.css'
  export default {
    name: 'repoDataTable',
    props: ['repoList','status','pageSize'],
    data(){
      return{
        tableData:this.repoList,
        showData:[],
        accountsStatus:this.status,
        detailPath:''
      }
    },
    mounted(){/**/
      console.log(this.repoList);

      this.getDataByStatus();
      this.getDataByPageNum(0);
    },
    watch:{
      repoList(curVal){
          console.log(curVal);
        this.tableData=curVal;
        this.getDataByStatus();
        this.getDataByPageNum(0);
      }
    },
    computed: {
      state () {
        return store.state;
      },
      constantData () {
        return constantData;
      },
    },
    methods:{
      currentChange(val){
        this.getDataByPageNum(val - 1)
      },
      getDataByStatus(){/*Tab*/
        switch(this.status){
          case 'all':break;
          case 'inForResponse':/**/
            var res=[];
            for(var i=0;i<this.tableData.length;i++ ){
              var item = this.tableData[i];
              if(item.curRepoBusiStatus===constantData.INFORRESPONSE){
                res.push(item)
              }
            }
            this.tableData = res;break;
          case 'forIn':/**/
            var res=[];
            for(var i=0;i<this.tableData.length;i++ ){
              var item = this.tableData[i];
              if(item.curRepoBusiStatus===constantData.FORIN){
                res.push(item)
              }
            }
            this.tableData = res;break;
          case 'alreadyIn':/**/
            var res=[];
            for(var i=0;i<this.tableData.length;i++ ){
              var item = this.tableData[i];
              if(item.curRepoBusiStatus===constantData.ALREADYIN){
                res.push(item)
              }
            }
            this.tableData = res;break;
          case 'forOut':/**/
            var res=[];
            for(var i=0;i<this.tableData.length;i++ ){
              var item = this.tableData[i];
              if(item.curRepoBusiStatus===constantData.FOROUT){
                res.push(item)
              }
            }
            this.tableData = res;break;
            case 'alreadyOut':/**/
            var res=[];
            for(var i=0;i<this.tableData.length;i++ ){
              var item = this.tableData[i];
              if(item.curRepoBusiStatus===constantData.ALREADYOUT){
                res.push(item)
              }
            }
            this.tableData = res;break;
          default:break;
        }
      },
      getDataByPageNum(pageNum){
        if((pageNum + 1) * this.pageSize > this.tableData.length){
          this.showData = this.tableData.slice(pageNum * this.pageSize);
        }else {
          this.showData = this.tableData.slice(pageNum * this.pageSize,(pageNum + 1)*this.pageSize);
        }
      },

      checkDetail (checkId) {
//        alert(this.tableData);

        store.commit('setCheckIdRepo',checkId);
        console.log(store.state.checkIdRepo);
        this.$router.push("/warehousing/repoDetails");
      },
    }
  }
</script>

<style>
</style>



