<template>
  <div>
    <el-breadcrumb separator=">" class="bread">
      <svg class="icon combinedShape" aria-hidden="true">  <use xlink:href="#icon-locate"></use></svg>
      <el-breadcrumb-item :to="{ path: '/' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item :to="{ path: '/logistics/myWaybill'}"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <el-card>
      <el-row class="el-row-header statePosition">
        <el-col class="sellerColor stateShow " :span="8"><svg class="icon" aria-hidden="true">  <use xlink:href="#icon-zhuangtai"></use></svg>
          {{logisticsDetail.waybillStatusCode | wayBillStatus}}</el-col>
        <el-col :span="8"><el-button type="primary" size="small" v-if="logisticsDetail.waybillStatusCode===constantData.FORSEND" @click="sendGood(logisticsDetail.orderNo)"></el-button></el-col>
      </el-row>
      <el-row>
        <el-col :span="24">
          <el-card class="box-card mybox" style="width:100%">
            <div slot="header" class="clearfix el-row-header">
              <svg class="icon detailIcon" aria-hidden="true">  <use xlink:href="#icon-wl_H"></use></svg>
              <span class="keynote"></span>
            </div>
            <div class="box-card mycard1 detailContent">
              <el-row>
                <el-col :span="6" class="msgName keynote">{{logisticsDetail.wayBillNo | nullSituation}}</el-col>
                <el-col :span="6" class="msgName">{{logisticsDetail.logisticsEnterpriseName | nullSituation}}</el-col>
                <el-col :span="6" class="msgName">{{logisticsDetail.inRepoTime | timeTransfer | nullSituation}}</el-col>
              </el-row>
              <el-row>
                <el-col :span="6" class="msgName"></el-col>
              </el-row>

              <el-row class="collapseTop">
                <template v-for="(item,index) in logisticsDetail.operationRecordVo">
                  <el-row class="status-list" :class="{circleBlue:index==(logisticsDetail.operationRecordVo.length-1)}">
                    <el-col :span="6" :class="{colorBlue:index==(logisticsDetail.operationRecordVo.length-1)}"><span>{{item.state | wayBillStatus}}:{{item.operateTime | timeTransfer}}</span></el-col>
                  </el-row>
                </template>
              </el-row>

            </div>
          </el-card>
        </el-col>
      </el-row>
      <el-row>
        <el-col :span="24">
          <el-card class="box-card mybox" style="width:100%">
            <div slot="header" class="clearfix el-row-header">
              <svg class="icon detailIcon" aria-hidden="true">  <use xlink:href="#icon-qita"></use></svg>
              <span class="keynote"></span>
            </div>
            <div class="box-card mycard1 detailContent ">
              <el-row class="msgName keynote"></el-row>
              <el-row class="cutoff">
                <el-col :span="6" class="msgName">{{logisticsDetail.wayBillNo | nullSituation}}</el-col>
                <el-col :span="6" class="msgName">{{logisticsDetail.senderRepoEnterpriseName}}</el-col>
                <el-col :span="6" class="msgName">{{logisticsDetail.senderRepoCertNo}}</el-col>
              </el-row>
              <el-row class="msgName keynote" style="margin-top: 10px"></el-row>
              <el-row class="cutoff">
                <el-col :span="6" class="msgName">{{logisticsDetail.receiverEnterpriseName}}</el-col>
                <el-col :span="6" class="msgName">{{logisticsDetail.receiverRepoEnterpriseName}}</el-col>
              </el-row>
              <el-row class="msgName keynote" style="margin-top: 10px"></el-row>
              <el-row>
                <el-col :span="6" class="msgName">{{logisticsDetail.productName}}</el-col>
                <el-col :span="6" class="msgName">{{logisticsDetail.productQuantity}}</el-col>
                <el-col :span="6" class="msgName">(){{(logisticsDetail.productQuantity===''||logisticsDetail.productQuantity===0)?0:logisticsDetail.productValue/logisticsDetail.productQuantity}}</el-col>
              </el-row>
            </div>
          </el-card>
        </el-col>
      </el-row>
    </el-card>
  </div>

</template>
<script>
  import store from '../../vuex/store'
  import constantData from '../../../../common/const'
  export default {
    name:'index',
    data () {
      return {
          logisticsDetail:{
              inRepoTime:'',
            productQuantity:'',
            operationRecordVo:[],
          }
      }
    },
    computed:{
      constantData () {
          return constantData;
      }
    },
    methods:{
        sendGood (checkId) {
            store.commit('setCheckIdOrder',checkId);
            this.$router.push("/logistics/deliver");
        }
    },
    mounted () {
      document.body.scrollTop = 0;
      document.documentElement.scrollTop = 0;
//        
        this.$http.get("../v1/waybill/wayBillDetail?orderNo="+store.state.checkIdOrder).then(function(res){
            console.log(res.body);
            this.logisticsDetail=res.body.data;
            this.logisticsDetail.inRepoTime='';
          for(var item in this.logisticsDetail.operationRecordVo){
              var temp=this.logisticsDetail.operationRecordVo[item];
            if(temp.state===constantData.ARRIVED){/**/
              this.logisticsDetail.inRepoTime=temp.operateTime;
              break;
            }
          }
        },function(err) {
            console.log(err);
        });
    }
  }
</script>



