<template>
  <div id="menu">
    <el-menu default-active="/shouye" class="el-menu-demo" mode="vertical" @select="" :router="true">
      <el-menu-item index="/" style="background-color: rgb( 242,246,248)">
        <svg class="icon menuImag" aria-hidden="true" style="font-size: 16px">  <use xlink:href="#icon-home_G"></use></svg>
      </el-menu-item>
      <div>
        <el-submenu index="/cangchu">
          <template slot="title"><svg class="icon menuImag" aria-hidden="true">  <use xlink:href="#icon-cc_H"></use></svg></template>
          <el-menu-item index="/warehousingCompany/myRepo/all" @click="setTab('all')"></el-menu-item>
          <el-menu-item index="/warehousingCompany/myRepo/inResponse" @click="setTab('inResponse')"></el-menu-item>
          <el-menu-item index="/warehousingCompany/myRepo/inConfirm" @click="setTab('inConfirm')"></el-menu-item>
          <el-menu-item index="/warehousingCompany/myRepo/outConfirm" @click="setTab('outConfirm')"></el-menu-item>
        </el-submenu>
        <el-submenu index="/cangdan">
        <template slot="title"><svg class="icon menuImag" aria-hidden="true">  <use xlink:href="#icon-order_H"></use></svg></template>
          <el-menu-item index="/warehousingCompany/myReceipt"></el-menu-item>
        </el-submenu>
      </div>
      </el-menu>
  </div>
</template>

<script>
  import Store from '../vuex/store.js'
export default {
  name: 'menu',
  data () {
    return {
      msg: 'Welcome to Your Vue.js App'
    }
  },
  methods:{
      setTab(status){
        document.body.scrollTop = 0;
        document.documentElement.scrollTop = 0;
        Store.commit('setRepoCompanyTab',status);
      }
  }
}
</script>

<style scoped>
</style>



