<template>
  <div id="list">
    <el-breadcrumb separator=">" class="bread">
      <svg class="icon combinedShape" aria-hidden="true">  <use xlink:href="#icon-locate"></use></svg>
      <el-breadcrumb-item :to="{ path: '/' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <el-card>
    <el-tabs  v-bind:value="state.accountsStatus">
      <el-tab-pane label="" name="all">
        <accounts-table :accountInfo="accountsList" status="0" pageSize="10" :isBuyer="state.isBuyer"> </accounts-table>
      </el-tab-pane>
      <el-tab-pane label="" name="signout" v-if="state.isBuyer === 'false'">
        <accounts-table :accountInfo="orderList" status="2" pageSize="10" > </accounts-table>
      </el-tab-pane>
      <el-tab-pane label="" name="accept" >
        <accounts-table :accountInfo="accountsList" status="21" pageSize="10" :isBuyer="state.isBuyer"> </accounts-table>
      </el-tab-pane>
      <el-tab-pane label="" name="cash" v-if="state.isBuyer === 'true'">
        <accounts-table :accountInfo="accountsList" status="26" pageSize="10" :isBuyer="state.isBuyer"> </accounts-table>
      </el-tab-pane>
      <el-tab-pane label="" name="discount" v-if="state.isBuyer === 'false'">
        <accounts-table :accountInfo="accountsList" status="26" pageSize="10" :isBuyer="state.isBuyer"> </accounts-table>
      </el-tab-pane>
      <el-tab-pane label="" name="other" v-if="state.isBuyer === 'false'" >
        <accounts-table :accountInfo="accountsList" status="41" pageSize="10" :isBuyer="state.isBuyer"> </accounts-table>
      </el-tab-pane>
    </el-tabs>
    </el-card>
  </div>
</template>
<script>
  import Store from '../../vuex/store.js'
  import AccountsTable from '../../components/accountsTable.vue'
  import constantData from '../../../../common/const'

  export default {
    name:'list',
    mounted () {
      document.body.scrollTop = 0;
      document.documentElement.scrollTop = 0;
      if(Store.state.isBuyer == 'false'){
        this.getSignoutOrderList()
      }
      this.getAccountsList()
    },
    created: function () {

      /*if(Store.state.isBuyer == 'false'){
        this.getSignoutOrderList()
      }
      this.getAccountsList()*/
    },
    components:{
      AccountsTable
    },
    computed: {
      state () {
        return Store.state;
      },
      constantData () {
        return constantData;
      }
    },
    data () {
      return {
        accountsList:[],
        orderList:[]
      }
    },
    methods:{
      //
      getSignoutOrderList(){
        this.$http.get('/v1/order/order_list/1',{emulateJSON:true}).then((res) => {
          console.log(res.body);
          var code =  res.body.code;
          var data =  res.body.data;
          if(code != 0){
            return;
          }
          for(var i=0;i<data.length;i++){
              var item = data[i]
;             if(item.transactionStatus == constantData.CONFIRMED){
                this.orderList.push(item);
            }
          }
        },(err) => {
          console.log(err);
        })
      },
      //
      getAccountsList(){
        var role = this.getRole();
        this.$http.post('/v1/receivable/receivableSimpleDetailList',{roleCode:role},{emulateJSON:true}).then((res) => {
          console.log(res.body);
          var code =  res.body.code;
          var data =  res.body.data;
          if(code != 0){
            return;
          }
          this.accountsList = data;
        },(err) => {
          console.log(err);
        })
      },
      getRole(){
          if(Store.state.isBuyer == 'true'){
              return 0
          }else{
              return 1
          }
      }
    },
  }
</script>



