<template>
  <div id="signout" class="signout">
    <el-card>
      <div>    <!--slot="header" class="clearfix"-->
        <span class="buyerStepTitle">1. </span>
      </div>
      <el-row class="dataTable">
        <el-row class="el-row-header">
          <el-col :span="6" style="margin-left: 19px;">{{orderDetail.txDetail.orderId}}</el-col>
          <el-col :span="6">{{orderDetail.txDetail.operationRecordVoList[0].operateTime | timeTransfer}}</el-col>
        </el-row>
        <el-row class="el-row-content" >
          <el-row class="el-row-content">
            <el-col :span="6" style="margin-left: 19px;">{{orderDetail.txDetail.payerAccount}}</el-col>
            <el-col :span="6">{{orderDetail.txDetail.productTotalPrice}}</el-col>
            <el-col :span="6"></el-col>
          </el-row>
          <el-row class="el-row-content">
            <el-col :span="6"  style="margin-left: 19px;">{{orderDetail.txDetail.productName}}</el-col>
            <el-col :span="6" >{{orderDetail.txDetail.productQuantity}}</el-col>
            <el-col :span="6">{{orderDetail.txDetail.payerRepo}}</el-col>
            <el-col :span="4">{{orderDetail.txDetail.payeeRepoCertNo}}</el-col>
          </el-row>
          <el-row class="el-row-content">
            <el-col :span="6" style="margin-left: 19px;">{{orderDetail.txDetail.payerBank}}</el-col>
            <el-col :span="6">{{orderDetail.txDetail.payerAccount}}</el-col>
          </el-row>
        </el-row>
      </el-row>
      <div>
        <span class="buyerStepTitle">2. </span>
      </div>
      <el-card class="signoutMsg">
        <el-form :label-position="labelPosition" :model="signoutInfo" :rules="signoutRules" ref="signoutInfo">
          <el-row>
            <el-col :span="8">
              <el-form-item label="" prop="isseAmt">
                <el-input v-model="signoutInfo.isseAmt">{{signoutInfo.isseAmt}}</el-input>
              </el-form-item>
            </el-col>
            <el-col :span="8">
              <el-form-item label="" prop="dueDt">
                <el-date-picker
                  v-model="signoutInfo.dueDt"
                  type="date"
                  placeholder=""
                  :picker-options="pickerOptions">
                </el-date-picker>
              </el-form-item>
            </el-col>
          </el-row>
          <el-row>
            <el-col :span="8">
              <el-form-item label="(%)" prop="rate">
                <el-input v-model="signoutInfo.rate">{{signoutInfo.rate}}</el-input>
              </el-form-item>
            </el-col>
          </el-row>
          <el-row>
            <el-col :span="8">
              <el-form-item label="" prop="pyerName">
                <el-input v-model="signoutInfo.pyerName" :disabled="true">{{signoutInfo.pyerName}}</el-input>
              </el-form-item>
            </el-col>
            <el-col :span="8">
              <el-form-item label="" prop="pyerBank">
                <el-input v-model="signoutInfo.pyerBank" :disabled="true">{{signoutInfo.pyerBank}}</el-input>
              </el-form-item>
            </el-col>
          </el-row>
          <el-row>
            <el-col :span="16" class="pyer">
              <el-form-item label="" prop="pyer">
                <el-input v-model="signoutInfo.pyer" :disabled="true">{{signoutInfo.pyer}}</el-input>
              </el-form-item>
            </el-col>
          </el-row>
          <el-row>
            <el-col :span="8">
              <el-form-item label="" prop="pyeeName" >
                <el-input v-model="signoutInfo.pyeeName" :disabled="true">{{signoutInfo.pyeeName}}</el-input>
              </el-form-item>
            </el-col>
            <el-col :span="8">
              <el-form-item label="" prop="pyeeBank" :disabled="true">
                <el-select v-model="signoutInfo.pyeeBank" name="" placeholder="">
                  <el-option :label="signoutInfo.pyeeBank" :value="signoutInfo.pyeeBank">{{signoutInfo.pyeeBank}}</el-option>
                </el-select>
              </el-form-item>
            </el-col>
          </el-row>
          <el-row>
            <el-col :span="16" class="pyee" >
              <el-form-item label="" prop="pyee">
                <el-select v-model="signoutInfo.pyee" name="1" placeholder="">
                  <el-option :label="signoutInfo.pyee" :value="signoutInfo.pyee"></el-option>
                </el-select>
              </el-form-item>
            </el-col>
          </el-row>
          <el-row>
            <el-col :span="8">
              <el-form-item label="" prop="contractNo">
                <el-input v-model="signoutInfo.contractNo">{{signoutInfo.contractNo}}</el-input>
              </el-form-item>
            </el-col>
            <el-col :span="8">
              <el-form-item label="" prop="invoiceNo">
                <el-input v-model="signoutInfo.invoiceNo">{{signoutInfo.invoiceNo}}</el-input>
              </el-form-item>
            </el-col>
          </el-row>
        </el-form>
        <el-row>
          <el-col :span="12">
            <el-button type="primary" @click="signout('signoutInfo')"></el-button>
          </el-col>
        </el-row>
      </el-card>
    </el-card>
  </div>
</template>
<script>
  import '../../../../assets/css/style.css'
  import Store from '../../vuex/store'
  import LocalStore from '../../../../common/store'
  import constantData from '../../../../common/const'

  export default {
    name:'signout',
    mounted:function () {
      document.body.scrollTop = 0;
      document.documentElement.scrollTop = 0;
      var userInfo = LocalStore.fetchUserInfo();
      this.signoutInfo.pyeeBank = userInfo.acctSvcrName
      this.signoutInfo.pyee = userInfo.acctIds
      this.getOrderDetail()
    },
    data () {
      return {
        pickerOptions: {
          disabledDate(time) {
            return time.getTime() < Date.now() - 8.64e7;
          }
        },
        orderDetail:{
          txDetail:{
            orderId:'',
            payerAccount:'',
            productTotalPrice:'',
            productName:'',
            productQuantity:'',
            payerRepo:'',
            payeeRepoCertNo:'',
            payerBank:'',
            payerAccount:'',
            operationRecordVoList:[{}]
          }
        },
        signoutInfo:{
          isseAmt:'',    //
          dueDt:'',      //
          rate:'',       //
          contractNo:'', //
          invoiceNo:'',   //
          pyerName:'',   //
          pyeeName:'',   //
          pyerBank:'',   //
          pyeeBank:'',    //
          pyer:'',       //
          pyee:''       //
        },
        options: [{
          value: '1',
          label: ''
        }, {
          value: '2',
          label: ''
        }, {
          value: '3',
          label: ''
        }],
        signoutRules:{
          dueDt: [
            { type: 'date',required: true, message: '', trigger: 'blur' }
          ],
          rate: [
            { required: true, message: '', trigger: 'blur' }
          ],
          pyeeBank: [
            { required: true, message: '', trigger: 'blur' }
          ],
          pyee:[
            { required: true, message: '', trigger: 'blur' }
          ]
        }
      }
    },
    methods:{
      signout(formName){
        this.$refs[formName].validate((valid) => {
          if (valid) {
            var data = new Date(this.signoutInfo.dueDt);
            var signParam = {
              orderNo:this.orderDetail.txDetail.orderId,
              pyer:this.signoutInfo.pyer,
              pyee:this.signoutInfo.pyee,
              isseAmt:this.signoutInfo.isseAmt,
              dueDt:data.getTime(),
              rate:this.signoutInfo.rate,
              contractNo:this.signoutInfo.contractNo,
              invoiceNo:this.signoutInfo.invoiceNo
            };
            this.$http.post('/v1/receivable/sign',signParam,{emulateJSON:true}).then((res) => {
              console.log(res.body.data);
              var code =  res.body.code;
              if(code != 0){
                return;
              }
              //
              Store.commit('setCheckIdRece',res.body.data);
              this.$router.push('/allAccounts/signout/detail')
            },(err) => {
              console.log(err);
            })
          } else {
            return false;
          }
        },(err) => {
          console.log(err);
        })
      },
      getOrderDetail(){
          console.log("the state checkIdOrder is:" + Store.state.checkIdOrder);
          this.$http.get("../v1/order/detail?orderNo=" + Store.state.checkIdOrder).then(
            function (res) {
              console.log(res.body.data);
              this.orderDetail = res.body.data;
              //
              this.signoutInfo.isseAmt = this.orderDetail.txDetail.productTotalPrice;
              this.signoutInfo.pyeeName = this.orderDetail.txDetail.payeeCompanyName;
              this.signoutInfo.pyer = this.orderDetail.txDetail.payerAccount;
              this.signoutInfo.pyerName = this.orderDetail.txDetail.payerCompanyName;
              this.signoutInfo.pyerBank = this.orderDetail.txDetail.payerBank;
            }, function (res) {
              console.log(res);
            }
          );
      }
    }
  }
</script>



